.alert {
    position: absolute;
    height: 4rem;
    width: 100%;
    top: -4rem;
    left:0;
    visibility: hidden;
    background-color: rgba(254,233,233, 0.92);
    z-index: 9999;
    border-radius: 1.4rem;
    display: grid;
    place-items: center;
    color: #620404;
    transition: all 0.5s ease-in-out;
}